import SearchBox from 'components/search-box/search-box';
import SearchResult from 'components/search-result/search-result';
import BookList from 'components/book-list/book-list';
import UniLoadMore from 'components/uni-load-more/uni-load-more';
import { searchMixin } from 'common/utils/mixin';
import { getHotBookList } from 'common/api/book';
import { ERR_OK } from 'common/api/config';

export default {
	mixins: [searchMixin],

	data() {
		return {
			bookList: [],
			pullDown: false,
			pullUp: false,
			loading: false,
			loadingType: 'loading'
		};
	},

	onLoad() {
		uni.showLoading({
			title: '加载中',
			mask: false
		});

		this._getHotBookList();
	},

	onPullDownRefresh() {
		if (this.searchResultShow) {
			this.pullDown = true;
		} else {
			let pullDown = true;
			this._getHotBookList(pullDown);
		}
	},

	onReachBottom() {
		if (this.searchResultShow) {
			this.pullUp = true;
		} else {
			this.loading = true;

			setTimeout(() => {
				this.loadingType = 'noMore';
			}, 1000);
		}
	},

	methods: {
		pullDone(e) {
			// 下拉完成后将 pullDown 设为 false，确保每次下拉时，传入结果组件的 pullDown 值都为 true
			this.pullDown = false;
			this.pullUp = false;
		},

		_getHotBookList(pullDown) {
			getHotBookList().then(res => {
				if (res.statusCode === ERR_OK) {
					this.bookList = res.data;

					if (pullDown) {
						// 如果为下拉刷新，结束后提示
						uni.showToast({
							title: '刷新成功',
							icon: 'success',
							duration: 1000
						});
					}
				}
			});
		}
	},

	components: {
		// 组件定义不能混入
		BookList,
		SearchBox,
		SearchResult,
		UniLoadMore
	}
};
